import React, { Component, useContext } from "react";
import Popup, { Props } from "reactjs-popup";
import { Box, BoxProps, HeadingProps, Heading, Text, TextProps, Button, ButtonProps, ResponsiveContext } from "grommet";

type NewProps = Omit<Props, "children">;

export interface AssertivePopupProps extends NewProps {
    open: boolean;
    title?: string;
    message: string;
    action: () => void;
    pad?: "none" | "xxsmall" | "xsmall" | "small" | "medium" | "large" | "xlarge" | {bottom?: "xxsmall" | "xsmall" | "small" | "medium" | "large" | "xlarge" | string,horizontal?: "xxsmall" | "xsmall" | "small" | "medium" | "large" | "xlarge" | string,left?: "xxsmall" | "xsmall" | "small" | "medium" | "large" | "xlarge" | string,right?: "xxsmall" | "xsmall" | "small" | "medium" | "large" | "xlarge" | string,top?: "xxsmall" | "xsmall" | "small" | "medium" | "large" | "xlarge" | string,vertical?: "xxsmall" | "xsmall" | "small" | "medium" | "large" | "xlarge" | string} | string;
    outerBoxProps?: BoxProps;
    headingProps?: HeadingProps;
    messageProps?: TextProps;
    buttonProps?: ButtonProps;
    size?: string;

    borderRadius?: string;
    shadow?: string;
};

export default class AssertivePopup extends Component<AssertivePopupProps> {

    constructor(p: AssertivePopupProps) {
        super(p);
    }

    public render() {
        return (
            <Popup position="center center" closeOnDocumentClick={true} 
                onClose={() => {
                    this.setState({open: false});
                    console.log("Still closing...");
                }}
                {...this.props}
                contentStyle={{
                    width: this.props.size === "small" ? "60%" : "300px",
                    height: this.props.size === "small" ? "280px" : "320px",
                    borderRadius: this.props.borderRadius ? this.props.borderRadius : "10px",
                    boxShadow: this.props.shadow ? this.props.shadow : "6px 7px 13px -3px rgba(0,0,0,0.5)",
                    ...this.props.contentStyle
                }}
                open={this.props.open}
            >
                <Box width="100%" height="100%">
                    <Box pad={this.props.pad ? this.props.pad : {
								vertical: "10px",
								horizontal: this.props.size === "small" ? "10px" : "20px"	
                            }} 
                        align="start" 
                        width="100%" height="100%" 
                        justify="between"
                    >
                        <Heading level="3" {...this.props.headingProps}>
                            {this.props.title ? this.props.title : "Are you sure ?"}
                        </Heading>

                        <Box alignSelf="stretch">
                            <Text weight={300} style={{fontSize: "16px"}} {...this.props.messageProps}>
                                {this.props.message}
                            </Text>	
                        </Box>

                        <Box direction="row" width="100%" justify="start" margin={{top: "40px"}} alignSelf="end" gap="large">
                            <Button primary={false} color="secondary" label="Yes"
                                onClick={() => {
                                    if (this.props.onClose) {
                                        this.props.onClose();
                                    }

                                    if (this.props.action) {
                                        this.props.action();
                                    }
                                }}
                                {...this.props.buttonProps}
                            />
                            <Button primary={true} color="secondary" label="No" style={{color: "#fff"}}
                                onClick={() => {
                                    if (this.props.onClose) {
                                        this.props.onClose();
                                    }
                                }}
                                {...this.props.buttonProps}
                            />
                        </Box>
                    </Box> 
                </Box>
            </Popup>
        )}
}